import '../styles/Project.sass'
import React from 'react'
import ScrollAnimation from "react-animate-on-scroll";
import Chip from '@mui/material/Chip'
import "../styles/Skill.sass"

export function Project(props) {

    let array = [];

    props.chipLabels?.forEach((chipLabel) => {
        array.push(<Chip className="mr-3" variant={"outlined"} style={{color: '#ff3a3a', backgroundColor: 'transparent', borderColor: '#ff3a3a', borderWidth: '1px'}} label={chipLabel} color={"primary"}/>)
    });
    return <ScrollAnimation animateIn={"fadeInUp"} animateOut={"fadeOut"} duration={0.4 + props.delay ? props.delay : 0.} className={"project mb-5 eurostile"}>
            <div className={"d-flex"}>
                <ScrollAnimation className="col-5 p-0 " animateIn={"fadeInLeft"}>
                    {props.children}
                </ScrollAnimation>
                <div className={"col-7 d-flex flex-column justify-content-between pl-4"}>
                    <div className={"title-project d-flex justify-content-end"}>{props.title}</div>
                    <div>
                        <div className={"eurostile mb-4 skill-title"}>Skills
                        </div>

                        <div className={"mt-2 mb-2 d-flex"}>
                            {array}
                        </div>
                    </div>
                    <div className={"content-project"}>
                        {props.content}
                        <span className={"text-right"}>{props?.download}</span>
                    </div>
                </div>
            </div>
    </ScrollAnimation>

}
